    text-align: center;

    z-index: 1;
    line-height: 50px;
    padding: 15px 25px 15px 25px !important;

    color: #fff !important;
}

.btn-box::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    font-weight: 400;
    opacity: 0;
    color: #fd562a;
    background-color: #fff;
    border: none;
    transition: all 0.4s;
    -webkit-transform: scale(0.2, 1);
    transform: scale(0.2, 1);
    z-index: -1;
}

.btn-box:hover::before {
    opacity: 1;
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
    font-weight: 400;
}

